import { Component, OnInit } from '@angular/core';
import {UrlinfoserviceService} from '../service/urlinfoservice.service';
import {HttpserviceService} from '../service/httpservice.service';

@Component({
  selector: 'app-newitementry',
  templateUrl: './newitementry.component.html',
  styleUrls: ['./newitementry.component.css']
})
export class NewitementryComponent implements OnInit {

  oldName:string;
  newName:string;
  id:number=0;
  headerList=['Sr.','Old Name','New Name','Delete'];
  itemUpdateList=[];
  constructor(private urlinfoservice:UrlinfoserviceService, private httpservice: HttpserviceService) { 

  }

  ngOnInit() {
  }
  getItemInfo(){
    let itemList = this.httpservice.getProducts(this.urlinfoservice.GET_ITEM_INFO_URL,'acd');
    console.log(itemList);
  }
  addItem(){
    if((this.oldName == undefined) && (this.newName == undefined)){
      return;
    }
    this.id = this.id + 1;
    let item = {id:this.id,oldName:this.oldName, newName:this.newName,remove:'Remove'};
    this.itemUpdateList.push(item);
    this.oldName=undefined;
    this.newName=undefined;
    document.getElementById("oldNameInput").focus();

  }
  removeItem(id:number){
    console.log('item to be removed:'+id);
    let tempItem=[];
    for(let el of this.itemUpdateList){
      if(el.id != id){
        tempItem.push(el);
      }
      if(el.id > id){
        el.id = el.id -1;
      }
    }
    this.itemUpdateList = tempItem;
    this.id = this.id - 1;
    tempItem = [];
  }
  saveOrUpdateItem(){
    console.log('Item updated to db');
    this.itemUpdateList = [];
    this.id = 0;
  }

}
